import React, { Component } from 'react';
import Web3 from 'web3';
import './App.css';
import AuctionABI from './contract/AuctionABI.json';

const CONTRACT_ADDRESS = '0x0e971fc7127fb5d8226fa887ea24225cf69c1a4a';
const SEPOLIA_CHAIN_ID = '0xaa36a7';

// Application data store (plain JS object, no React state)
const appData = {
  web3: null,
  contract: null,
  connectedAccount: '',
  contractOwner: '',
  contractBalance: '0',
  ownerFunds: '0',
  stopped: false,
  auctions: [],
  pendingReturns: '0',
  eventSubscriptions: []
};

class App extends Component {
  componentDidMount() {
    this.initWeb3();
  }

  ccomponentWillUnmount() {
    appData.eventSubscriptions.forEach(sub => {
      if (sub && sub.unsubscribe) sub.unsubscribe();
    });
  }


  async initWeb3() {
    if (typeof window.ethereum !== 'undefined') {
      try {
        const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
        appData.web3 = new Web3(window.ethereum);
        appData.connectedAccount = accounts[0] ? accounts[0].toLowerCase() : '';
        appData.contract = new appData.web3.eth.Contract(AuctionABI, CONTRACT_ADDRESS);

        // Check network
if (!window.ethereum || !window.ethereum.isMetaMask) {
  alert('Please use MetaMask');
  return;
}

const chainId = await window.ethereum.request({ method: 'eth_chainId' });

if (chainId !== SEPOLIA_CHAIN_ID) {
  await window.ethereum.request({
    method: 'wallet_switchEthereumChain',
    params: [{ chainId: SEPOLIA_CHAIN_ID }]
  });
}

        // Setup MetaMask listeners
        window.ethereum.on('accountsChanged', (accounts) => {
          appData.connectedAccount = accounts[0] ? accounts[0].toLowerCase() : '';
          this.loadContractData();
        });

        window.ethereum.on('chainChanged', () => {
          window.location.reload();
        });

        await this.loadContractData();
        this.subscribeToEvents();
      } catch (error) {
        console.error('Web3 initialization failed:', error);
        this.updateUI();
      }
    } else {
      alert('Please install MetaMask');
      this.updateUI();
    }
  }

  async loadContractData() {
    // Prevent multiple concurrent loads  if already loading, skip
    if (this._isLoading) return;
    this._isLoading = true;

    try {
      const contract = appData.contract;
      const web3 = appData.web3;

      // Load contract state
      appData.contractOwner = (await contract.methods.owner().call()).toLowerCase();
      appData.stopped = await contract.methods.stopped().call();
      appData.ownerFunds = await contract.methods.ownerFunds().call();
      appData.contractBalance = await web3.eth.getBalance(CONTRACT_ADDRESS);

      // Load pending returns for connected account
      if (appData.connectedAccount) {
        appData.pendingReturns = await contract.methods.pendingReturns(appData.connectedAccount).call();
      }

      // Load all auctions
      const auctionCount = await contract.methods.auctionCount().call();
      appData.auctions = [];
      for (let i = 1; i <= Number(auctionCount); i++) {
        const auction = await contract.methods.auctions(i).call();
        appData.auctions.push({
          id: i,
          title: auction.title,
          seller: auction.seller.toLowerCase(),
          startPrice: auction.startPrice,
          highestBid: auction.highestBid,
          highestBidder: auction.highestBidder.toLowerCase(),
          endTime: Number(auction.endTime),
          canceled: auction.canceled,
          fulfilled: auction.fulfilled
        });
      }

      this.updateUI();
    } catch (error) {
      console.error('Failed to load contract data:', error);
      this.updateUI();
    } finally {
      this._isLoading = false;
    }
  }

  subscribeToEvents() {
    const contract = appData.contract;

    // AuctionCreated
    const auctionCreatedSub = contract.events.AuctionCreated({ fromBlock: 'latest' })
      .on('data', () => this.loadContractData());
    appData.eventSubscriptions.push(auctionCreatedSub);

    // BidPlaced
    const bidPlacedSub = contract.events.BidPlaced({ fromBlock: 'latest' })
      .on('data', () => this.loadContractData());
    appData.eventSubscriptions.push(bidPlacedSub);

    // AuctionCanceled
    const auctionCanceledSub = contract.events.AuctionCanceled({ fromBlock: 'latest' })
      .on('data', () => this.loadContractData());
    appData.eventSubscriptions.push(auctionCanceledSub);

    // AuctionFinalized
    const auctionFinalizedSub = contract.events.AuctionFinalized({ fromBlock: 'latest' })
      .on('data', () => this.loadContractData());
    appData.eventSubscriptions.push(auctionFinalizedSub);

    // Withdraw
    const withdrawSub = contract.events.Withdraw({ fromBlock: 'latest' })
      .on('data', () => this.loadContractData());
    appData.eventSubscriptions.push(withdrawSub);

    // OwnerWithdraw
    const ownerWithdrawSub = contract.events.OwnerWithdraw({ fromBlock: 'latest' })
      .on('data', () => this.loadContractData());
    appData.eventSubscriptions.push(ownerWithdrawSub);

    // ContractDestroyed
    const contractDestroyedSub = contract.events.ContractDestroyed({ fromBlock: 'latest' })
      .on('data', () => this.loadContractData());
    appData.eventSubscriptions.push(contractDestroyedSub);
  }

  updateUI() {
    const web3 = appData.web3;

    // Header
    const walletEl = document.getElementById('connected-wallet');
    const ownerEl = document.getElementById('contract-owner');
    const balanceEl = document.getElementById('contract-balance');
    const ownerFundsEl = document.getElementById('owner-funds');

    if (walletEl) walletEl.textContent = appData.connectedAccount || 'Not connected';
    if (ownerEl) ownerEl.textContent = appData.contractOwner || 'Loading...';
    if (balanceEl) balanceEl.textContent = web3 ? web3.utils.fromWei(appData.contractBalance, 'ether') + ' ETH' : '0 ETH';
    if (ownerFundsEl) ownerFundsEl.textContent = web3 ? web3.utils.fromWei(appData.ownerFunds, 'ether') + ' ETH' : '0 ETH';

    // Create Auction button state
    const createBtn = document.getElementById('create-auction-btn');
    if (createBtn) {
      const isOwner = appData.connectedAccount === appData.contractOwner;
      createBtn.disabled = isOwner || appData.stopped;
    }

    // Live Auctions
    this.renderLiveAuctions();

    // Fulfilled Auctions
    this.renderFulfilledAuctions();

    // Canceled Auctions
    this.renderCanceledAuctions();

    // Owner Panel visibility
    const ownerPanel = document.getElementById('owner-panel');
    if (ownerPanel) {
      const isOwner = appData.connectedAccount === appData.contractOwner;
      ownerPanel.style.display = isOwner ? 'block' : 'none';
    }

    // Disable owner controls if stopped
    const ownerControls = document.querySelectorAll('.owner-control');
    ownerControls.forEach(el => {
      el.disabled = appData.stopped;
    });

    // Claim button state
    const claimBtn = document.getElementById('claim-btn');
    if (claimBtn) {
      const hasPending = appData.pendingReturns.toString() !== '0';
      claimBtn.disabled = !hasPending;
    }

    const pendingEl = document.getElementById('pending-returns');
    if (pendingEl && web3) {
      pendingEl.textContent = web3.utils.fromWei(appData.pendingReturns, 'ether') + ' ETH';
    }
  }

  renderLiveAuctions() {
    const container = document.getElementById('live-auctions-list');
    if (!container) return;

    const now = Math.floor(Date.now() / 1000);
    const liveAuctions = appData.auctions.filter(a => !a.canceled && !a.fulfilled && a.endTime > now);
    const web3 = appData.web3;

    if (liveAuctions.length === 0) {
      container.innerHTML = '<p>No live auctions</p>';
      return;
    }

    let html = '';
    liveAuctions.forEach(auction => {
      const remaining = auction.endTime - now;
      const isHighestBidder = appData.connectedAccount === auction.highestBidder;
      const canCancel = appData.connectedAccount === auction.seller || appData.connectedAccount === appData.contractOwner;
      const highestBidEth = web3 ? web3.utils.fromWei(auction.highestBid, 'ether') : '0';

      html += `
        <div class="auction-card">
          <h4>${auction.title}</h4>
          <p><strong>Seller:</strong> ${auction.seller}</p>
          <p><strong>Highest Bid:</strong> ${highestBidEth} ETH</p>
          <p><strong>Remaining:</strong> ${remaining}s</p>
          ${isHighestBidder ? '<p class="highlight"> You are highest bidder</p>' : ''}
          <div class="auction-actions">
            <input type="text" id="bid-amount-${auction.id}" placeholder="ETH amount" />
            <button onclick="window.appInstance.placeBid(${auction.id})">Bid</button>
            ${canCancel ? `<button onclick="window.appInstance.cancelAuction(${auction.id})">Cancel</button>` : ''}
          </div>
        </div>
      `;
    });

    container.innerHTML = html;
  }

  renderFulfilledAuctions() {
    const container = document.getElementById('fulfilled-auctions-list');
    if (!container) return;

    const now = Math.floor(Date.now() / 1000);
    const web3 = appData.web3;

    // Fulfilled = auction.fulfilled === true
    const fulfilledAuctions = appData.auctions.filter(a => a.fulfilled);

    // Pending fulfillment = time expired, has bids, not canceled, not fulfilled
    const pendingFulfillment = appData.auctions.filter(a => 
      !a.canceled && !a.fulfilled && a.endTime <= now && a.highestBidder !== '0x0000000000000000000000000000000000000000'
    );

    let html = '';

    // Show pending fulfillment first
    pendingFulfillment.forEach(auction => {
      const canFulfill = appData.connectedAccount === auction.seller || appData.connectedAccount === appData.contractOwner;
      const highestBidEth = web3 ? web3.utils.fromWei(auction.highestBid, 'ether') : '0';

      html += `
        <div class="auction-card pending-fulfill">
          <h4>${auction.title} (Awaiting Fulfillment)</h4>
          <p><strong>Seller:</strong> ${auction.seller}</p>
          <p><strong>Winner:</strong> ${auction.highestBidder}</p>
          <p><strong>Final Bid:</strong> ${highestBidEth} ETH</p>
          ${canFulfill ? `<button onclick="window.appInstance.fulfillAuction(${auction.id})">Fulfill</button>` : ''}
        </div>
      `;
    });

    // Show already fulfilled
    fulfilledAuctions.forEach(auction => {
      const highestBidEth = web3 ? web3.utils.fromWei(auction.highestBid, 'ether') : '0';

      html += `
        <div class="auction-card fulfilled">
          <h4>${auction.title} (Fulfilled)</h4>
          <p><strong>Seller:</strong> ${auction.seller}</p>
          <p><strong>Winner:</strong> ${auction.highestBidder}</p>
          <p><strong>Final Bid:</strong> ${highestBidEth} ETH</p>
        </div>
      `;
    });

    container.innerHTML = html || '<p>No fulfilled auctions</p>';
  }

  renderCanceledAuctions() {
    const container = document.getElementById('canceled-auctions-list');
    if (!container) return;

    const canceledAuctions = appData.auctions.filter(a => a.canceled);
    const web3 = appData.web3;

    if (canceledAuctions.length === 0) {
      container.innerHTML = '<p>No canceled auctions</p>';
      return;
    }

    let html = '';
    canceledAuctions.forEach(auction => {
      const highestBidEth = web3 ? web3.utils.fromWei(auction.highestBid, 'ether') : '0';

      html += `
        <div class="auction-card canceled">
          <h4>${auction.title} (Canceled)</h4>
          <p><strong>Seller:</strong> ${auction.seller}</p>
          <p><strong>Last Bid:</strong> ${highestBidEth} ETH</p>
        </div>
      `;
    });

    container.innerHTML = html;
  }

  // Contract interaction methods
  async createAuction() {
    const titleEl = document.getElementById('auction-title');
    const priceEl = document.getElementById('auction-price');
    const durationEl = document.getElementById('auction-duration');

    const title = titleEl ? titleEl.value : '';
    const priceEth = priceEl ? priceEl.value : '0';
    const duration = durationEl ? durationEl.value : '0';

    if (!title || !priceEth || !duration) {
      alert('Please fill all fields');
      return;
    }

    try {
      const web3 = appData.web3;
      const priceWei = web3.utils.toWei(priceEth, 'ether');
      const feeWei = web3.utils.toWei('0.02', 'ether');

      await appData.contract.methods.createAuction(title, priceWei, duration).send({
        from: appData.connectedAccount,
        value: feeWei
      });
      await this.loadContractData();

      // Clear inputs
      if (titleEl) titleEl.value = '';
      if (priceEl) priceEl.value = '';
      if (durationEl) durationEl.value = '';
    } catch (error) {
      console.error('Create auction failed:', error);
      alert('Failed to create auction: ' + error.message);
    }
  }

  async placeBid(auctionId) {
    const inputEl = document.getElementById(`bid-amount-${auctionId}`);
    const amountEth = inputEl ? inputEl.value : '0';

    if (!amountEth || parseFloat(amountEth) <= 0) {
      alert('Please enter a valid bid amount');
      return;
    }

    try {
      const web3 = appData.web3;
      const amountWei = web3.utils.toWei(amountEth, 'ether');

      await appData.contract.methods.bid(auctionId).send({
        from: appData.connectedAccount,
        value: amountWei
      });
      await this.loadContractData();

      if (inputEl) inputEl.value = '';
    } catch (error) {
      console.error('Bid failed:', error);
      alert('Failed to place bid: ' + error.message);
    }
  }

  async cancelAuction(auctionId) {
    try {
      await appData.contract.methods.cancelAuction(auctionId).send({
        from: appData.connectedAccount
      });
      await this.loadContractData();
    } catch (error) {
      console.error('Cancel auction failed:', error);
      alert('Failed to cancel auction: ' + error.message);
    }
  }

  async fulfillAuction(auctionId) {
    try {
      await appData.contract.methods.finalizeAuction(auctionId).send({
        from: appData.connectedAccount
      });
      await this.loadContractData();
    } catch (error) {
      console.error('Fulfill auction failed:', error);
      alert('Failed to fulfill auction: ' + error.message);
    }
  }

  async claimFunds() {
    try {
      await appData.contract.methods.withdraw().send({
        from: appData.connectedAccount
      });
      await this.loadContractData();
    } catch (error) {
      console.error('Claim failed:', error);
      alert('Failed to claim funds: ' + error.message);
    }
  }

  async withdrawOwnerFunds() {
    try {
      await appData.contract.methods.withdrawOwnerFunds().send({
        from: appData.connectedAccount
      });
      await this.loadContractData();
    } catch (error) {
      console.error('Owner withdraw failed:', error);
      alert('Failed to withdraw owner funds: ' + error.message);
    }
  }

  async changeOwner() {
    const inputEl = document.getElementById('new-owner-address');
    const newOwner = inputEl ? inputEl.value : '';

    if (!newOwner) {
      alert('Please enter new owner address');
      return;
    }

    try {
      await appData.contract.methods.changeOwner(newOwner).send({
        from: appData.connectedAccount
      });
      await this.loadContractData();
      if (inputEl) inputEl.value = '';
    } catch (error) {
      console.error('Change owner failed:', error);
      alert('Failed to change owner: ' + error.message);
    }
  }

  async banSeller() {
    const inputEl = document.getElementById('ban-address');
    const address = inputEl ? inputEl.value : '';

    if (!address) {
      alert('Please enter address to ban');
      return;
    }

    try {
      await appData.contract.methods.banUser(address).send({
        from: appData.connectedAccount
      });
      await this.loadContractData();
      if (inputEl) inputEl.value = '';
    } catch (error) {
      console.error('Ban seller failed:', error);
      alert('Failed to ban seller: ' + error.message);
    }
  }

  async destroyContract() {
    if (!window.confirm('Are you sure you want to destroy the contract? This cannot be undone.')) {
      return;
    }

    try {
      await appData.contract.methods.destroyContract().send({
        from: appData.connectedAccount
      });
      await this.loadContractData();
    } catch (error) {
      console.error('Destroy contract failed:', error);
      alert('Failed to destroy contract: ' + error.message);
    }
  }

  render() {
    // Expose instance globally for onclick handlers
    window.appInstance = this;

    return (
      <div className="app-container">
        {/* SECTION 1: HEADER */}
        <header className="header-section">
          <h1>Decentralized Auction Platform</h1>
          <div className="header-info">
            <p><strong>Connected Wallet:</strong> <span id="connected-wallet">Not connected</span></p>
            <p><strong>Contract Owner:</strong> <span id="contract-owner">Loading...</span></p>
            <p><strong>Contract Balance:</strong> <span id="contract-balance">0 ETH</span></p>
            <p><strong>Owner Fees:</strong> <span id="owner-funds">0 ETH</span></p>
          </div>
        </header>

        {/* SECTION 2: NEW AUCTION */}
        <section className="section">
          <h2>New Auction</h2>
          <div className="form-group">
            <input type="text" id="auction-title" placeholder="Title (unique)" />
            <input type="text" id="auction-price" placeholder="Start Price (ETH)" />
            <input type="text" id="auction-duration" placeholder="Duration (seconds)" />
            <button id="create-auction-btn" onClick={() => this.createAuction()}>
              Create Auction (0.02 ETH fee)
            </button>
          </div>
        </section>

        {/* SECTION 3: LIVE AUCTIONS */}
        <section className="section">
          <h2>Live Auctions</h2>
          <div id="live-auctions-list">
            <p>Loading...</p>
          </div>
        </section>

        {/* SECTION 4: FULFILLED AUCTIONS */}
        <section className="section">
          <h2>Fulfilled Auctions</h2>
          <div id="fulfilled-auctions-list">
            <p>Loading...</p>
          </div>
        </section>

        {/* SECTION 5: CANCELED AUCTIONS */}
        <section className="section">
          <h2>Canceled Auctions</h2>
          <div id="canceled-auctions-list">
            <p>Loading...</p>
          </div>
          <div className="claim-section">
            <p><strong>Your Pending Returns:</strong> <span id="pending-returns">0 ETH</span></p>
            <button id="claim-btn" onClick={() => this.claimFunds()}>
              Claim
            </button>
          </div>
        </section>

        {/* SECTION 6: OWNER CONTROL PANEL */}
        <section id="owner-panel" className="section owner-section" style={{ display: 'none' }}>
          <h2>Owner Control Panel</h2>
          <div className="owner-controls">
            <button className="owner-control" onClick={() => this.withdrawOwnerFunds()}>
              Withdraw Owner Funds
            </button>
            <div className="form-group">
              <input type="text" id="new-owner-address" placeholder="New Owner Address" />
              <button className="owner-control" onClick={() => this.changeOwner()}>
                Change Owner
              </button>
            </div>
            <div className="form-group">
              <input type="text" id="ban-address" placeholder="Address to Ban" />
              <button className="owner-control" onClick={() => this.banSeller()}>
                Ban Seller
              </button>
            </div>
            <button className="owner-control destroy-btn" onClick={() => this.destroyContract()}>
              Destroy Contract
            </button>
          </div>
        </section>
      </div>
    );
  }
}

export default App;
